<template>
<!--
  v-model
  1:value="num"
  2@input="num = $event"  $eventinput
 -->
  <el-select
    :value="value"
    @input="$emit('input', $event)"
    placeholder=""
  >
    <el-option v-if="$route.path === '/index/article'" :value="null" ></el-option>
    <el-option
      v-for="channel in channels"
      :key="channel.id"
      :label="channel.name"
      :value="channel.id"
    >
    </el-option>
  </el-select>
</template>

<script>
export default {
  name: 'channel',
  data () {
    return {
      channels: []
    }
  },
  props: {
    value: {
      type: [String, Number]
    }
  },

  methods: {
    loadChannels () {
      this.$axios({
        method: 'get',
        url: '/channels'
      }).then(res => {
        this.channels = res.data.data.channels
      }).catch(err => {
        console.log(err, '')
      })
    }
  },

  created () {
    this.loadChannels()
  }
}
</script>

