<template>
  <div>
    <el-card>
      <div slot="header">
        <span></span>
      </div>
      <el-form ref='filterForm' :model="filterForm" >
        <el-form-item label="">
          <el-radio-group v-model="filterForm.status">
            <el-radio-button :label="null"></el-radio-button>
            <el-radio-button label="0"></el-radio-button>
            <el-radio-button label="1"></el-radio-button>
            <el-radio-button label="2"></el-radio-button>
            <el-radio-button label="3"></el-radio-button>
            <el-radio-button label="4"></el-radio-button>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="" >
          <channel-list v-model="filterForm.channel_id"></channel-list>
        </el-form-item>
        <el-form-item label="">
          <el-date-picker
            v-model="rangeDate"
            type="daterange"
            unlink-panels
            range-seperator="----"
            start-placeholde=""
            end-placeholde=""
            value-format="yyyy-MM-dd"
          ></el-date-picker>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click='filterArticles'></el-button>
        </el-form-item>
      </el-form>
    </el-card>
    <el-card>
      <div slot="header">
        <span>{{ total_count }}</span>
      </div>
      <!--
        el-table 
        data 
         data  v-for
        
          data 
          
          
        el-table-column 
          prop 
          label 
          width 

        
       -->
      <el-table
        :data="tableData"
        stripe
        style="width: 100%"
        v-loading="loading"
      >
        <el-table-column
          prop="avatar"
          label=""
        >
        <!--
          
           template  slot-scope="scope"  scope.row 
          scope.row  v-for  item
          
          -->
          <template slot-scope="scope">
            <img :src="scope.row.cover.images[0] || ''" alt="" width='50'>
          </template>
        </el-table-column>
        <el-table-column
          prop="title"
          label=""
        ></el-table-column>
        <el-table-column
          prop="status"
          label=""
        >
          <template slot-scope="scope">
            <el-tag
              :type="articleStatus[scope.row.status].type"
            >
              {{ articleStatus[scope.row.status].label }}
            </el-tag>
          </template>
        </el-table-column>
        <el-table-column
          prop="pubdate"
          label=""
        ></el-table-column>
        <el-table-column
          prop=""
          label=""
        >
          <template slot-scope="scope">
            <el-button type="primary" size="mini" @click="$router.push(`/index/publish/${scope.row.id}`)"></el-button>
            <el-button type="danger" size="mini" @click="onDelete(scope.row.id)"></el-button>
          </template>
        </el-table-column>
      </el-table>
      <!--  -->
      <el-pagination
        style="text-align: center;margin: 50px"
        background
        layout="prev, pager, next"
        :total="total_count"
        :current-page="filterForm.page"
        @current-change="pageChange"
        :disabled="loading"
      ></el-pagination>
    </el-card>
  </div>
</template>

<script>
// import channel component
import channelList from '@/views/article/article-channel'

export default {
  name: 'article-list',
  data () {
    return {
      filterForm: {
        status: null,
        channel_id: null,
        begin_pubdate: null,
        end_pubdate: null,
        page: 1,
        per_page: 10
      },
      rangeDate: '',
      tableData: [],
      articleStatus: [
        { type: 'primary', label: '' },
        { type: 'warning', label: '' },
        { type: 'success', label: '' },
        { type: 'danger', label: '' },
        { type: 'info', label: '' }
      ],
      total_count: 0,
      loading: true
    }
  },
  methods: {
    loadArticles () {
      //  
      this.loading = true
      this.$axios({
        method: 'get',
        url: '/articles',
        // headers: {
        //   Authorization: `Bearer ${window.localStorage.getItem('toutiao-token')}`
        // },
        params: this.filterForm
      }).then(res => {
        this.total_count = res.data.data.total_count
        this.tableData = res.data.data.results
      }).catch(err => {
        console.log(err, '')
      }).finally(() => {
        // 
        this.loading = false
      })
    },

    pageChange (currentPage) {
      this.filterForm.page = currentPage
      this.loadArticles()
    },

    // 
    filterArticles () {
      // filterFormpage 1
      this.filterForm.page = 1
      this.loadArticles()
    },

    // 
    onDelete (articleId) {
      this.$confirm('?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this.$axios({
          method: 'delete',
          url: `/articles/${articleId}`
        }).then(res => {
          this.loadArticles()
        }).catch(err => {
          console.log(err, '')
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        })
      })
    }
  },

  watch: {
    // 
    rangeDate () {
      this.filterForm.begin_pubdate = this.rangeDate && this.rangeDate[0]
      this.filterForm.end_pubdate = this.rangeDate && this.rangeDate[1]
    }
  },

  components: {
    channelList
  },

  created () {
    this.loadArticles()
  }
}
</script>

<style>

</style>
