<template>
  <el-card>
    <div slot="header">
      <span></span>
      <el-button style="float: right;padding: 3px 0" type="text"></el-button>
    </div>
    <el-table :data="comments" style="width: 100%">
      <el-table-column label="">
        <template slot-scope="scope">
          <img :src="scope.row.aut_photo" alt="" width="50">
        </template>
      </el-table-column>
      <el-table-column prop="content" label=""></el-table-column>
      <el-table-column prop="name" label="">
        <template slot-scope="scope">
          {{ scope.row.is_liking === 1 ? '' : '' }}
        </template>
      </el-table-column>
      <el-table-column prop="like_count" label=""></el-table-column>
      <el-table-column prop="pubdate" label="">
        <template slot-scope="scope">
          {{ scope.row.pubdate | dateFormat('YYYY-MM-DD') }}
        </template>
      </el-table-column>
      <el-table-column label="">
        <template slot-scope="scope">
          <el-switch
            v-model="scope.row.is_top"
            active-color="#13ce66"
            inactive-color="#ff4949"
            @change="onRecommend(scope.row)"
          ></el-switch>
        </template>
      </el-table-column>
      <el-table-column prop="reply_count" label=""></el-table-column>
    </el-table>
  </el-card>
</template>

<script>
export default {
  name: 'CommentDetail',
  data () {
    return {
      comments: []
    }
  },
  //  this.$route.params 
  //  props  props 
  //  data 
  props: {
    commentId: {
      type: String,
      required: true
    }
  },
  created () {
    this.loadComments()
  },
  methods: {
    loadComments () {
      this.$axios({
        method: 'GET',
        url: 'comments',
        params: {
          type: 'a',
          source: this.commentId
        },
        data: {
          allow_comment: true
        }
      }).then(res => {
        this.comments = res.data.data.results
      }).catch(() => {
        this.$message.error('')
      })
    },

    onRecommend (comment) {
      this.$axios({
        method: 'PUT',
        url: `/comments/${comment.com_id}/sticky`,
        data: {
          sticky: comment.is_top
        }
      }).then(res => {
        this.$message('')
      }).catch(() => {
        this.$message.error('')
      })
    }
  }
}
</script>

